.profile-content {
    margin-top: 20px;
}
.ant-table-thead > tr > th{
    background: white !important;
}

.card-content-total {
    width: 248px;
    height: 90px;
    background: white url(../../img/profile/profile-total-value-bg.png) no-repeat center right;
    background-size: 85px 85px;
    border-radius: 16px;
    padding: 20px 0 20px 20px;
    // background-size: auto auto;
}

.card-header {
    font-size: 12px;
    font-weight: 500;
    color: #909DB4;
    line-height: 12px;
    display:flex;
    align-items: center;

    img {
        width: 20px;
        height: 20px;
        margin-right: 4px;
    }
}

.card-footer {
    margin-top: 14px;

    span {
        display: inline-block;
        transform: scale(1);
    }

    font-size: 18px;
    font-weight: 600;
    color: #000000;
}

.card-list-item {
    width: 516px;
    display: flex;
    justify-content: space-between;
}
.card-content-staked{
    width:516px;
    background: white url(../../img/profile/profile-staked-bg.png) no-repeat center right;
    background-size: 85px 85px;
    margin-top: 20px;
}
.card-content-price{
    background: white url(../../img/profile/profile-price-bg.png) no-repeat center right;
    background-size: 85px 85px;
}


