/** @format */

.manageRight-close-container {
  width: 400px;
  padding: 20px 16px;
  background: @component-background;
  border-radius: 8px;
  // margin-left: 20px;

  .tx-fee {
    padding: 20px 20px;
    background-color: @input-back;
    border-radius: 8px;
    .item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
      .tx-fee-text {
        font-size: 12px;
        font-weight: 600;
        color: @modal-close-color;
      }

      .tx-fee-price {
        font-size: 12px;
        font-weight: 500;
        color: @heading-color;
      }
      &:nth-last-child(1) {
        margin-bottom: 0;
      }
    }
  }

  .close {
    margin-top: 20px;
    width: 100%;
    height: 48px;
    font-size: 16px !important;
    font-weight: 600;
    color: #fff;
    background: #005aff;
    // box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    &:hover {
      background: #0d8fff;
    }
    span {
      font-size: 16px !important;
    }
  }
  .ant-btn[disabled] {
    span {
      color: #737373;
    }
    &:hover {
      background: rgba(0, 0, 0, 0.05);
    }
  }
  .ant-btn[disabled],
  .ant-btn[disabled]:hover {
    background: rgba(0, 0, 0, 0.05);
    color: #fff;
  }
}

@media (max-width: 750px) {
  .manageRight-close-container {
    width: 100%;
    margin-left: 0;
    margin-top: 20px;
    .amount {
      padding: 10px;
    }
    .confirm-order {
      width: 100%;
    }
  }
}

